<template>
  <div class="px-4 py-4">
    <h3 class="mb-3">포장 검사 관리</h3>
    <hr>
    <!-- 검사조건 부분 시작 -->
    <div class="mb-4">
      <div class="d-flex align-items-center mb-3">
        <h4 class="me-3">검색 조건</h4>
      </div>

      <div class="row gx-3 p-4 rounded border shadow search-background">
        <!-- 날짜 범위 -->
        <div class="col-md-2 ps-5">
          <label for="startDate" class="form-label">등록일(부터)</label>
          <div class="d-flex gap-2">
            <input type="date" id="startDate" class="form-control border p-2 cursor-pointer"
              v-model="searchInfo.startDate" autocomplete="off" />
          </div>
        </div>
        <div class="col-md-2">
          <label for="endDate" class="form-label">등록일(까지)</label>
          <div class="d-flex gap-2">
            <input type="date" id="endDate" class="form-control border p-2 cursor-pointer" v-model="searchInfo.endDate"
              autocomplete="off" />
          </div>
        </div>

        <!-- 제품명 -->
        <div class="col-md-3">
          <label for="pName" class="form-label">제품명</label>
          <input type="search" id="pName" class="form-control border p-2 cursor-pointer" placeholder="제품명"
            v-model="searchInfo.pName" autocomplete="off" />
        </div>

        <!-- 검색 버튼 -->
        <div class="col-md-2 d-flex align-items-end">
          <material-button size="md" v-on:click="searchOrder">검색</material-button>
          <material-button size="md" class="m-4" color="info" v-on:click="searchRequestAll">전체 조회</material-button>
        </div>
      </div>
    </div>
  </div>
  <!-- 검사조건 부분 끝 -->
  <hr>
  <!-- 검사결과 시작 -->
  <div class="container-fluid py-4">
    <h4>검사 상세 정보</h4>
    <div class="ps-4">
      <p>검사할 제품을 선택하고 이상이 있을 시 불량 사유와 불량 수량을 입력하세요.</p>
    </div>

    <div class="grid-container">
      <ag-grid-vue :rowData="rowData1" :columnDefs="columnDefs" :theme="theme" :defaultColDef="defaultColDef"
        @grid-ready="onGridReady" @cell-clicked="onCellClicked" :pagination="true"
        :paginationPageSizeSelector="[10, 20, 50, 100]" :paginationPageSize="10"
        :noRowsOverlayComponent="noRowsOverlayComponent">
      </ag-grid-vue>
    </div>

  </div>
  <!-- 검사결과 끝 -->

  <hr>



  <!-- 검사처리내역 시작 -->
  <div class="container-fluid py-4">
    <h4>검사 처리 내역</h4>
    <div class="grid-container">
      <ag-grid-vue :rowData="rowData2" :columnDefs="columnDefs" :theme="theme" :defaultColDef="defaultColDef"
        @grid-ready="onGridReady" @cell-clicked="onCellClicked" :pagination="true"
        :paginationPageSizeSelector="[10, 20, 50, 100]" :paginationPageSize="10" style="height: 400px;"
        :noRowsOverlayComponent="noRowsOverlayComponent">
      </ag-grid-vue>
    </div>
    <hr>
    <div class="row justify-content-center">
      <div class="col-auto">
        <material-button class="btn btn-success" size="lg" @click="openModal">저장</material-button>
      </div>
      <div class="col-auto">
        <material-button class="btn btn-warning" size="lg" @click="reset">초기화</material-button>
      </div>
    </div>
  </div>
  <!-- 검사처리내역 끝 -->

  <!-- 불량 상세 모달 -->

  <Modal :isShowModal="showModalRJC" @closeModal="closeModal"
    @confirm="saveDefectDetailsForRow(selectedRow.qcProcessId, selectedRow.totalQnt)">
    <template v-slot:title>
      <h1 class="modal-title fs-5" id="exampleModalLabel">검사 상세 정보</h1>
    </template>
    <template v-slot:list>
      <div class="modal-css">
        <table class="table table-sm w-100" style="border: 3px solid #dee2e6;">
          <tbody>
            <tr>
              <th scope="row" style="width: 30%; text-align: left; border: 1px solid #dee2e6;">공정검사번호</th>
              <td style="text-align: right; border: 1px solid #dee2e6;">{{ selectedRow.qcProcessId }}</td>
            </tr>
            <tr>
              <th scope="row" style="text-align: left; border: 1px solid #dee2e6;">제품명</th>
              <td style="text-align: right; border: 1px solid #dee2e6;">{{ selectedRow.pName }}</td>
            </tr>
            <tr>
              <th scope="row" style="text-align: left; border: 1px solid #dee2e6;">총 수량</th>
              <td style="text-align: right; border: 1px solid #dee2e6;">{{ selectedRow.totalQnt }} 개</td>
            </tr>
          </tbody>
        </table>
        <div class="d-flex justify-content-end mt-3">
          <material-button size="md" class="mt-3 btn btn-warning"
            @click="addDefectDetailForRow(selectedRow.qcProcessId)">
            불량 항목 추가
          </material-button>
        </div>
        <h5 class="pt-5">불량 내역</h5>
        <!-- 불량 사유 입력 및 불량 수량 입력 -->
        <div class="pt-2" v-if="defectDetailsMap[selectedRow.qcProcessId] == null">
          <b>불량 내역이 없습니다.</b>
        </div>
        <div v-for="(detail, index) in defectDetailsMap[selectedRow.qcProcessId]" :key="index"
          class="defect-item  mt-3">
          <div class="form-group">
            <label :for="'reason' + index">불량 사유 {{ index + 1 }}:</label>
            <select v-model="detail.reason" :id="'reason' + index" class="form-control">
              <option value="" hidden disabled selected>(선택)</option>
              <option v-for="reason in defectReasons" :key="reason.code" :value="reason.code">
                {{ reason.name }}
              </option>
            </select>
          </div>
          <div class="form-group mt-2">
            <label :for="'defectQty' + index">불량 수량(개):</label>
            <input type="number" v-model="detail.qty" :id="'defectQty' + index" class="form-control"
              autocomplete="off" />
          </div>
          <div class="d-flex justify-content-end mt-3">
            <button class="btn btn-danger mt-2"
              @click="removeDefectDetailForRow(selectedRow.qcProcessId, index)">삭제</button>
          </div>
        </div>
      </div>
    </template>
  </Modal>

  <Modal :isShowModal="showModalDone" @closeModal="closeModal" @confirm="confirm">
    <template v-slot:title>
      <h1 class="modal-title fs-5" id="exampleModalLabel">검사 완료 확인</h1>
    </template>
    <template v-slot:list>
      <b>검사 내역대로 저장하시겠습니까?</b>
    </template>
  </Modal>

  <div style="display: none">
    <CustomNoRowsOverlay />
  </div>

</template>

<script>

import MaterialButton from "@/components/MaterialButton.vue";

import axios from 'axios';
import { ajaxUrl } from '@/utils/commons.js';
import userDateUtils from '@/utils/useDates.js';

import theme from "@/utils/agGridTheme";

import Modal from "@/views/natureBlendComponents/modal/ModalQc.vue";

import { useNotification } from "@kyvg/vue3-notification";  //노티 드리겠습니다
const { notify } = useNotification();  // 노티 내용변수입니다

import CustomNoRowsOverlay from "@/views/natureBlendComponents/grid/noDataMsg.vue";


export default {
  name: "입고검사관리",
  components: { MaterialButton, Modal, CustomNoRowsOverlay },
  data() {
    return {
      searchInfo: {
        pName: '',
        //범위 : 일주일전부터 오늘
        startDate: this.dateFormat(new Date(Date.now() - 7 * 24 * 60 * 60 * 1000), 'yyyy-MM-dd'),
        endDate: this.dateFormat(new Date(), 'yyyy-MM-dd')
      },



      searchList: [],

      //ag grid 관련
      noRowsOverlayComponent: 'CustomNoRowsOverlay',
      theme: theme,
      rowData1: [], //검색 결과(db를 통해 얻은 결과에서 골라서 부분 선택적으로 추가)
      columnDefs: [ //검색 결과 열
        { headerName: "공정검사번호", field: "qcProcessId", resizable: false, cellStyle: { textAlign: "center" }, flex: 1 },
        { headerName: "공정작업번호", field: "processNum", resizable: false, cellStyle: { textAlign: "right" }, flex: 1 },
        { headerName: "생산지시번호", field: "productionOrderNum", resizable: false, cellStyle: { textAlign: "center" }, flex: 1 },
        { headerName: "제품명", field: "pName", resizable: false, cellStyle: { textAlign: "left" }, flex: 1 },
        { headerName: "검사담당자", field: "eName", resizable: false, cellStyle: { textAlign: "left" }, flex: 1 },
        {
          headerName: "총 수량", field: "totalQnt", resizable: false, cellStyle: { textAlign: "right" }, flex: 1,
          cellRenderer: params => {
            if (params.value != null) {
              const formatted_t_qty = Number(params.value).toLocaleString() + ' 개';
              return `<span style="text-align: right;">${formatted_t_qty}</span>`;
            } else {
              return `<span style="text-align: right;"></span>`;
            }
          },
        },
        {
          headerName: "합격량", field: "passQnt", resizable: false, editable: true,
          cellStyle: {
            //border: "0.5px dashed #fb8c00", // 점선 테두리
            cursor: "text", // 텍스트 커서
            textAlign: "right",
          },
          flex: 1,
          cellRenderer: params => {
            if (params.value != null) {
              const formatted_t_qty = Number(params.value).toLocaleString() + ' 개';
              return `
                <div style="display: flex; align-items: center; justify-content: space-between;">
                  <i class="fas fa-edit" style=" color: gray; margin-right: 5px;"></i>
                  <span style="flex-grow: 1; text-align: right;">${formatted_t_qty}</span>
                </div>
              `;
            } else {
              return `<span style="text-align: right;"></span>`;
            }
          },
        },
        {
          headerName: "불합격량", field: "rjcQnt", resizable: false, editable: true,
          cellStyle: {
            //border: "0.5px dashed #fb8c00", // 점선 테두리
            cursor: "text", // 텍스트 커서
            textAlign: "right",
          },

          flex: 1,
          cellRenderer: params => {
            if (params.value != null) {
              const formatted_t_qty = Number(params.value).toLocaleString() + ' 개';
              return `
                <div style="display: flex; align-items: center; justify-content: space-between;">
                  <i class="fas fa-edit" style=" color: gray; margin-right: 5px;"></i>
                  <span style="flex-grow: 1; text-align: right;">${formatted_t_qty}</span>
                </div>
              `;
            } else {
              return `<span style="text-align: right;"></span>`;
            }
          },
        },
        { headerName: "검사시작시각", field: "inspecStart", resizable: false, cellStyle: { textAlign: "center" }, flex: 1.5 },
        { headerName: "검사상태", field: "inspecStatus", resizable: false, cellStyle: { textAlign: "left" }, flex: 1 },

      ],

      defaultColDef: {
        headerClass: "header-center"
      },

      //
      inspecData: [],
      showModalRJC: false, // (불량항목)모달 표시 여부
      selectedRow: {}, // 선택된 행 데이터
      defectDetails: [], // 불량 항목(불량코드) 여러 개 관리
      defectReasons: [    // 불량 사유 리스트
        // { code: "D001", name: "파손" },
        // { code: "D002", name: "오염" },
        // { code: "D003", name: "불량품" },
      ],
      selectedReason: "", // 선택된 불량 사유 코드
      defectQty: 0, // 불량 수량
      /// db에 보낼 제품 한건의 불량항목및 수량
      defectDetailsMap: {}, // { qcProcessId: [ { reason, qty }, ... ] }

      rowData2: [], //rowData1 중 검사상태(inspecStatus)가 '검사내역작성완료'인 것을 담음
      showModalDone: false,


    }

  },
  methods: {
    // 날짜를 YYYY-MM-DD 형식으로 변환
    dateFormat(value, format) {
      return userDateUtils.dateFormat(value, format);
    },

    onGridReady(params) {
      this.gridApi = params.api;
      // this.gridApi.sizeColumnsToFit();
    },


    //검색창 관련
    //조건 검색 시작  
    async searchOrder() {
      if (new Date(this.searchInfo.startDate) > new Date(this.searchInfo.endDate)) {
        `${notify({
          text: "시작 날짜는 종료 날짜보다 이전이어야 합니다.",
          type: "error", // success, warn, error 가능
        })}`;
        return;
      }

      const name = this.searchInfo.pName.replace(/\s+/g, "");
      const result = {
        pName: name.length != 0 ? name : "",
        startDate: this.searchInfo.startDate,
        endDate: this.searchInfo.endDate
      };
      let searchResult = await axios.post(`${ajaxUrl}/requestQCPP`, result)
        .catch(err => console.log(err));
      this.searchList = searchResult.data;

      // ag grid에 결과값 넣기
      this.rowData1 = [];
      this.defectDetailsMap = {};
      for (let i = 0; i < this.searchList.length; i++) {
        let col = {
          "qcProcessId": this.searchList[i].qc_packing_id,
          "processNum": this.searchList[i].process_num,                   //공정작업번호(바디)
          "productionOrderNum": this.searchList[i].production_order_num, //생산지시코드
          "pName": this.searchList[i].product_name,
          "eName": this.searchList[i].emp_name,
          "totalQnt": this.searchList[i].total_qnt,
          "passQnt": this.searchList[i].pass_qnt,
          "rjcQnt": this.searchList[i].rjc_qnt,
          "inspecStart": this.dateFormat(this.searchList[i].inspec_start, 'yyyy-MM-dd hh:mm:ss'),
          "inspecStatus": this.searchList[i].inspec_status

        }
        this.rowData1[i] = col;
      }
      this.rowData2 = [];
    },
    //조건 검색 끝


    //전체 조회 시작
    async searchRequestAll() {
      let searchResult = await axios.post(`${ajaxUrl}/requestQCPP`)
        .catch(err => console.log(err));
      this.searchList = searchResult.data;

      // ag grid에 결과값 넣기
      this.rowData1 = [];
      this.defectDetailsMap = [];
      for (let i = 0; i < this.searchList.length; i++) {
        let col = {
          "qcProcessId": this.searchList[i].qc_packing_id,
          "processNum": this.searchList[i].process_num,                   //공정작업번호(바디)
          "productionOrderNum": this.searchList[i].production_order_num, //생산지시코드
          "pName": this.searchList[i].product_name,
          "eName": this.searchList[i].emp_name,
          "totalQnt": this.searchList[i].total_qnt,
          "passQnt": this.searchList[i].pass_qnt,
          "rjcQnt": this.searchList[i].rjc_qnt,
          "inspecStart": this.dateFormat(this.searchList[i].inspec_start, 'yyyy-MM-dd hh:mm:ss'),
          "inspecStatus": this.searchList[i].inspec_status

        }
        this.rowData1[i] = col;
        this.rowData2 = [];
      }
    },
    //전체 조회 끝

    //신청 건의 합격량, 불합격량(불량항목, 각각의 수량) 처리
    onCellClicked(event) {
      // 선택된 행 데이터 저장 및 모달 표시
      this.selectedRow = event.data;
      this.showModalRJC = true;
    },
    closeModal() {
      this.showModalRJC = false;
      this.selectedRow = {};

      this.showModalDone = false;
    },




    ///신청 건의 불량항목 내역 관련
    addDefectDetailForRow(qcProcessId) {
      if (!this.defectDetailsMap[qcProcessId]) {
        this.defectDetailsMap[qcProcessId] = [];
      }
      this.defectDetailsMap[qcProcessId].push({ reason: "", qty: 0 });
    },
    removeDefectDetailForRow(qcProcessId, index) {
      if (this.defectDetailsMap[qcProcessId]) {
        this.defectDetailsMap[qcProcessId].splice(index, 1);
        if (this.defectDetailsMap[qcProcessId].length === 0) {
          delete this.defectDetailsMap[qcProcessId]; // 데이터가 비었으면 삭제
        }
      }
    },
    saveDefectDetailsForRow(qcProcessId, total) {
      const defectDetails = this.defectDetailsMap[qcProcessId] || [];
      if (defectDetails.some(detail => !detail.reason)) {
        notify({
          text: "불량 사유가 입력되지 않은 항목이 있습니다.",
          type: "warn", // success, warn, error 가능
        });
        return;
      }
      else if (defectDetails.some(detail => detail.qty <= 0)) {
        notify({
          text: "수량이 입력되지 않았거나 0 이하를 입력한 항목이 있습니다.",
          type: "warn", // success, warn, error 가능
        });
        return;
      }
      else if (defectDetails.some(detail => !Number.isInteger(detail.qty))) {
        notify({
          text: "해당 자재의 수량은 소수로 입력할 수 없습니다.",
          type: "warn", // success, warn, error 가능
        });
        return;
      }

      const rjcQntSum = defectDetails.reduce((sum, detail) => sum + detail.qty, 0);
      if (rjcQntSum > total) {
        notify({
          text: "불량 총합량이 총합량보다 클 수 없습니다.",
          type: "warn", // success, warn, error 가능
        });
        return;
      }

      const pass = total - rjcQntSum;
      // 업데이트 로직
      const rowIndex = this.rowData1.findIndex(row => row.qcProcessId === this.selectedRow.qcProcessId);
      if (rowIndex !== -1) {
        // 새 데이터 배열 생성 (Vue의 반응형 감지를 위해)
        this.rowData1 = this.rowData1.map((row, index) => {
          if (index === rowIndex) {
            return { ...row, passQnt: pass, rjcQnt: rjcQntSum, inspecStatus: '검사내역입력완료' };
          }
          return row;
        });
      }

      this.closeModal();
      this.rowData2 = this.rowData1.filter(row => row['inspecStatus'] === '검사내역입력완료')
      notify({
        text: `검사 결과 - 합격량: ${pass}개, 불합격량: ${rjcQntSum}개 입니다.`,
        type: "success", // success, warn, error 가능
      });
    },

    //최종 처리 버튼
    openModal() {
      if (this.rowData2.length == 0) {
        notify({
          text: "저장할 검사처리내역이 없습니다.",
          type: "error", // success, warn, error 가능
        });
        return;
      }
      this.showModalDone = !this.showModalDone
    },
    async confirm() {
      // 객체를 배열로 변환
      let defectDetailsArray = [];
      for (let qcId in this.defectDetailsMap) {
        if (Object.prototype.hasOwnProperty.call(this.defectDetailsMap, qcId)) {
          this.defectDetailsMap[qcId].forEach(detail => {
            defectDetailsArray.push({
              qcProcessId: qcId,
              faultyCode: detail.reason,
              qty: detail.qty,
            });
          });
        }
      }
      //배열 정렬
      defectDetailsArray.sort((a, b) => {
        if (a.qcProcessId === b.qcProcessId) {
          return a.faultyCode.localeCompare(b.faultyCode); // 검사번호 같으면 불량코드 비교
        }
        return a.qcProcessId.localeCompare(b.qcProcessId); // 검사번호 우선 비교
      });

      let qcData = {
        qcpp: this.rowData2,
        qcppr: defectDetailsArray,
      };
      let result = await axios.post(`${ajaxUrl}/completeQCPP`, qcData)
        .catch(err => console.log(err));
      notify({
        text: `완료된 검사: ${result.data.updatedRows}건, 기록된 불량 내역: ${result.data.defectNum}건`,
        type: "success", // success, warn, error 가능
      });



      this.closeModal();
      this.searchRequestAll();
    },


    //불량코드 불러오기
    async callFaultyCode() {
      let faultyCodeList = await axios.get(`${ajaxUrl}/faultyCodeQCPC`)
        .catch(err => console.log(err));
      this.faultyCodeList = faultyCodeList.data;
      const arrData = [];
      this.faultyCodeList.forEach((element, index) => {
        arrData[index] = { "code": element.faulty_code, "name": element.faulty_reason };

      });
      this.defectReasons = arrData;
    },

    //리셋
    reset() {
      if (this.rowData2.length != 0) {
        this.searchRequestAll();
        notify({
          text: `검사 처리 내역을 초기화 했습니다.`,
          type: "warn", // success, warn, error 가능
        });
      }
    }


  },
  created() {
    this.searchRequestAll();
    this.callFaultyCode();

  }


};
</script>




<style scoped lang="scss">
.pagination {
  display: flex;
  justify-content: center;
}

.container-fluid {
  min-height: 500px;

  .search {
    margin-top: 24px;
  }
}

//검색창 라벨
.mb-4 {
  label {
    font-weight: bold;
    margin-bottom: 5px;
    color: #333;
