<template>
  <div class="px-4 py-4">
    <h3>입고 검사-검사 신청</h3>
    <hr>
    <!-- 검사조건 부분 시작 -->
    <div class="mb-4">
      <div class="d-flex align-items-center mb-3">
        <h4 class="me-3">검색 조건</h4>
      </div>

      <div class="row gx-3 p-4 rounded border shadow search-background">
        <!-- 날짜 범위 -->
        <div class="col-md-2 ps-5">
          <label for="startDate" class="form-label">등록일(부터)</label>
          <div class="d-flex gap-2">
            <input type="date" id="startDate" class="form-control border p-2 cursor-pointer"
              v-model="searchInfo.startDate" autocomplete="off" />
          </div>
        </div>
        <div class="col-md-2">
          <label for="endDate" class="form-label">등록일(까지)</label>
          <div class="d-flex gap-2">
            <input type="date" id="endDate" class="form-control border p-2 cursor-pointer" v-model="searchInfo.endDate"
              autocomplete="off" />
          </div>
        </div>

        <!-- 자재명 -->
        <div class="col-md-3">
          <label for="mName" class="form-label">자재명</label>
          <input type="search" id="mName" class="form-control border p-2 cursor-pointer" placeholder="자재명"
            v-model="searchInfo.mName" autocomplete="off" />
        </div>

        <!-- 검색 버튼 -->
        <div class="col-md-2 d-flex align-items-end">
          <material-button size="md" v-on:click="searchOrder">검색</material-button>
          <material-button size="md" class="m-4" color="info" v-on:click="searchRequestAll">전체 조회</material-button>
        </div>

      </div>
    </div>
  </div>
  <!-- 검사조건 부분 끝 -->
  <hr>
  <!-- 검사결과 시작 -->
  <div class="container-fluid py-4 ">
    <h4>검색 결과</h4>
    <div class="ps-4">
      <p>자재 검사 신청 내역에 추가할 입고 예정 자재를 선택해 주세요.</p>
    </div>
    <div class="col-2 ps-4">
      <material-button class="btn-success" size="md" color="primary" @click="addCheckedRows">선택 항목 추가</material-button>
    </div>

    <div class="grid-container">
      <ag-grid-vue :rowData="rowData1" :columnDefs="columnDefs" :theme="theme" :defaultColDef="defaultColDef"
        @grid-ready="onReady" :pagination="true" :paginationPageSizeSelector="[10, 20, 50, 100]"
        :paginationPageSize="10" :noRowsOverlayComponent="noRowsOverlayComponent" rowSelection="multiple">
      </ag-grid-vue>

    </div>
  </div>
  <!-- 검사결과 끝 -->

  <hr>
  <!-- 신청내역 시작 -->
  <div class="container-fluid py-4">
    <h4>검사 신청 내역</h4>
    <div class="ps-4">
      <p>자재 검사 신청 내역에 있는 수량이 실제 수량과 다르다면 수정할 수 있습니다.</p>
    </div>
    <div class="grid-container">
      <ag-grid-vue :rowData="rowData2" :columnDefs="columnDefs2" :theme="theme" :defaultColDef="defaultColDef"
        @grid-ready="onGridReady" :pagination="true" @cell-clicked="onCellClicked"
        :paginationPageSizeSelector="[10, 20, 50, 100]" :paginationPageSize="10" style="height: 400px;"
        :noRowsOverlayComponent="noRowsOverlayComponent">
      </ag-grid-vue>


    </div>
    <!-- 신청내역 끝 -->
    <hr>
    <!-- 버튼 구역 -->
    <div class="row justify-content-center">
      <div class="col-auto">
        <material-button class="btn btn-success" size="lg" @click="openModal">저장</material-button>
      </div>
      <div class="col-auto">
        <material-button class="btn btn-warning" size="lg" @click="resetAll">초기화</material-button>
      </div>
    </div>
    <!-- 버튼 구역 끝-->
  </div>


  <Modal :isShowModal="isShowModalUpdate" @closeModal="closeModal"
    @confirm="updateQnt(selectedRow.orderCode, editedQuantity)">
    <template v-slot:title>
      <h1 class="modal-title fs-5" id="exampleModalLabel">발주 수량 확인</h1>
    </template>
    <template v-slot:list>
      <p>발주 수량을 확인하시고 다르다면 수정해 주세요.</p>
      <div class="form-group">
        <label for="quantityInput">실제 수량</label>
        <div class="d-flex align-items-center">
          <input v-model="editedQuantity" type="number" id="quantityInput" class="form-control" style="width: auto;"
            autocomplete="off" />
          <p class="ms-2 mb-0">{{ materialType }}</p>
        </div>
      </div>
    </template>
  </Modal>

  <Modal :isShowModal="isShowModal" @closeModal="closeModal" @confirm="confirm">
    <template v-slot:title>
      <h1 class="modal-title fs-5" id="exampleModalLabel">신청 확인</h1>
    </template>
    <template v-slot:list>
      <b>신청 내역대로 저장하시겠습니까?</b>
    </template>
  </Modal>

  <div style="display: none">
    <CustomNoRowsOverlay />
  </div>

</template>

<script>
import { toRaw } from 'vue';

import axios from 'axios';
import { ajaxUrl } from '@/utils/commons.js';
import userDateUtils from '@/utils/useDates.js';

import MaterialButton from "@/components/MaterialButton.vue";

import theme from "@/utils/agGridTheme";
import Modal from "@/views/natureBlendComponents/modal/ModalQc.vue";

import { AgGridVue } from "ag-grid-vue3";

import { useNotification } from "@kyvg/vue3-notification";  //노티 드리겠습니다
const { notify } = useNotification();  // 노티 내용변수입니다

import CustomNoRowsOverlay from "@/views/natureBlendComponents/grid/noDataMsg.vue";


export default {
  name: "입고검사신청",
  components: { MaterialButton, Modal, AgGridVue, CustomNoRowsOverlay },

  data() {
    return {
      //로그인 정보
      loginInfo: {},

      //검색창 및 결과 관련
      searchInfo: {
        mName: '',
        //범위 : 일주일전부터 오늘
        startDate: this.dateFormat(new Date(Date.now() - 7 * 24 * 60 * 60 * 1000), 'yyyy-MM-dd'),
        endDate: this.dateFormat(new Date(), 'yyyy-MM-dd')
      },
      //db를 통해 얻은 결과
      orderList: [],

      //모달 관련
      isShowModal: false,
      isShowModalUpdate: false,

      editedQuantity: 0, // 모달에서 수정한 실제 수량
      selectedRow: {}, // 수정할 행 정보

      //ag grid 관련
      noRowsOverlayComponent: 'CustomNoRowsOverlay',

      theme: theme,
      rowData1: [], //검색 결과(db를 통해 얻은 결과에서 골라서 부분 선택적으로 추가)
      columnDefs: [ //검색 결과 열
        {
          headerName: "",
          resizable: false,
          editable: true,
          sortable: false,
          cellStyle: { textAlign: "center" },
          flex: 0.0625,
          headerCheckboxSelection: true,
          checkboxSelection: true,
        },
        { headerName: "자재발주코드", field: "orderCode", resizable: false, cellStyle: { textAlign: "center" }, flex: 0.5 },
        { headerName: "자재명", field: "mName", resizable: false, cellStyle: { textAlign: "left" }, flex: 1 },
        {
          headerName: "발주수량", field: "ordQty", resizable: false, cellStyle: { textAlign: "right" }, flex: 0.5,
          cellRenderer: params => {
            if (params.value) {
              if (params.data.mName.includes('병')) {
                const formatted_t_qty = Number(params.value).toLocaleString() + ' 개';
                return `<span style="text-align: right;">${formatted_t_qty}</span>`;
              } else {
                const formatted_t_qty = Number(params.value * 0.001).toLocaleString() + ' kg';
                return `<span style="text-align: right;">${formatted_t_qty}</span>`;
              }
            } else {
              return `<span style="text-align: right;"></span>`;
            }
          },
        },
        { headerName: "발주신청일", field: "orderDate", resizable: false, cellStyle: { textAlign: "center" }, flex: 0.25 },
      ],
      rowData2: [],   //신청 내역
      columnDefs2: [
        { headerName: "자재발주코드", field: "orderCode", resizable: false, cellStyle: { textAlign: "center" }, flex: 0.5 },
        { headerName: "자재명", field: "mName", resizable: false, cellStyle: { textAlign: "left" }, flex: 1 },
        {
          headerName: "실제수량", field: "ordQty", resizable: false, editable: true,
          cellStyle: {
            //border: "0.5px dashed #fb8c00", // 점선 테두리
            cursor: "text", // 텍스트 커서
            textAlign: "right",
          },
          flex: 0.5,
          cellRenderer: params => {
            if (params.value != null) {
              const formatted_t_qty = (params.data.mName.includes('병') ? (`${Number(params.value).toLocaleString()}개`): (`${Number(params.value * 0.001).toLocaleString()} kg`));
              return `
                <div style="display: flex; align-items: center; justify-content: space-between;">
                  <i class="fas fa-edit" style=" color: gray; margin-right: 5px;"></i>
                  <span style="flex-grow: 1; text-align: right;">${formatted_t_qty}</span>
                </div>
              `;
            } else {
              return `<span style="text-align: right;"></span>`;
            }
          },
        },
        { headerName: "발주신청일", field: "orderDate", resizable: false, cellStyle: { textAlign: "center" }, flex: 0.3 },
        { headerName: "검사담당인", field: "empName", resizable: false, cellStyle: { textAlign: "left" }, flex: 0.3 },
        {
          cellRenderer: (params) => {
            return `
              <button class="btn btn-danger btn-sm delete-btn" data-id="${params.node.id}">
                삭제
              </button>
            `;
          },
          editable: false,
          resizable: false,
          sortable: false,
          filter: false,
          cellStyle: { textAlign: "center" }
          , flex: 0.25
        }
      ],
      defaultColDef: {
        headerClass: "header-center"
      },
    };
  },



  methods: {
    bringIoginInfo() {
      this.loginInfo = this.$store.state.loginInfo;
    },
    //검색창 관련
    async searchOrder() {
      if (new Date(this.searchInfo.startDate) > new Date(this.searchInfo.endDate)) {
        `${notify({
          text: "시작 날짜는 종료 날짜보다 이전이어야 합니다.",
          type: "error", // success, warn, error 가능
        })}`;
        return;
      }

      const name = this.searchInfo.mName.replace(/\s+/g, "");
      const result = {
        mName: name.length != 0 ? name : "",
        startDate: this.searchInfo.startDate,
        endDate: this.searchInfo.endDate
      };
      let searchResult = await axios.post(`${ajaxUrl}/meterialOrderQC`, result)
        .catch(err => console.log(err));
      this.orderList = searchResult.data;

      // ag grid에 결과값 넣기
      this.rowData1 = []
      for (let i = 0; i < this.orderList.length; i++) {
        let col = {
          "orderCode": this.orderList[i].order_code, "mName": this.orderList[i].material_name,
          "ordQty": this.orderList[i].ord_qty, "orderDate": this.dateFormat(this.orderList[i].order_date, 'yyyy-MM-dd')
          , "empNum": this.loginInfo.emp_num, "empName": this.loginInfo.name
        };
        this.rowData1[i] = col;
      }
    },

    //전체 조회
    async searchOrderAll() {
      let searchResult = await axios.get(`${ajaxUrl}/meterialOrderQCAll`)
        .catch(err => console.log(err));
      this.orderList = searchResult.data;

      // ag grid에 결과값 넣기
      this.rowData1 = []
      for (let i = 0; i < this.orderList.length; i++) {
        let col = {
          "orderCode": this.orderList[i].order_code, "mName": this.orderList[i].material_name,
          "ordQty": this.orderList[i].ord_qty, "orderDate": this.dateFormat(this.orderList[i].order_date, 'yyyy-MM-dd')
          , "empNum": this.loginInfo.emp_num, "empName": this.loginInfo.name
        }
        this.rowData1[i] = col;
      }
    },
    //추가용
    onReady(event) {
      this.gridApi1 = event.api;
    },
    //삭제용
    onGridReady(params) {
      this.gridApi = params.api;
      this.gridColumnApi = params.columnApi;

      // 기존 이벤트 리스너 제거
      document.removeEventListener("click", this.handleDeleteButtonClick);

      // 새 삭제 버튼 이벤트 리스너 등록
      this.handleDeleteButtonClick = (event) => {
        if (event.target && event.target.classList.contains("delete-btn")) {
          const rowIndex = event.target.getAttribute("data-id");
          this.deleteRow(rowIndex);
        }
      };
      document.addEventListener("click", this.handleDeleteButtonClick);
    },

    //검색 결과 관련
    // 체크한 항목을 rowData2(신청내역)에 추가
    addCheckedRows() {
      // 현재 선택된 행 가져오기
      const selectedNodes = this.gridApi1.getSelectedNodes(); // 선택된 노드
      const checkedRows = selectedNodes.map(node => node.data); // 선택된 노드의 데이터
      // 체크박스가 true인 항목만 필터링
      if (checkedRows.length == 0) {
        notify({
          text: "추가할 자재를 선택하지 않았습니다.",
          type: "error", // success, warn, error 가능
        });
        return;
      }

      // 중복되지 않게 추가하기 위해 기존 rowData2와 병합
      const newRows = checkedRows.filter(row =>
        !this.rowData2.some(existingRow => existingRow["orderCode"] === row["orderCode"])
      );
      this.rowData2 = [...this.rowData2, ...newRows]; // rowData2에 추가
      notify({
        text: `${newRows.length}개의 건이 추가되었습니다.`,
        type: "success", // success, warn, error 가능
      });
    },

    //생산내역 관련
    //삭제버튼 누를시 해당 행 삭제처리
    deleteRow(rowIndex) {
      const nodeToDelete = this.gridApi.getDisplayedRowAtIndex(rowIndex).data;
      // 데이터 삭제
      this.rowData2 = this.rowData2.filter((row) => row.orderCode !== nodeToDelete.orderCode);
      notify({
        text: `해당 건을 삭제했습니다.`,
        type: "error", // success, warn, error 가능
      });

    },
    //초기화
    resetAll() {
      if (this.rowData2.length != 0) {
        this.searchOrderAll();
        this.rowData2 = []; // 저장된 항목 초기화
        notify({
          text: `검사 처리 내역을 초기화 했습니다.`,
          type: "warn", // success, warn, error 가능
        });
      }

    },


    //기타 관련
    // 날짜를 YYYY-MM-DD 형식으로 변환
    dateFormat(value, format) {
      return userDateUtils.dateFormat(value, format);
    },
    //모달 열기
    openModal() {
      if (this.rowData2.length == 0) {
        notify({
          text: "신청 내역이 비었습니다.",
          type: "error", // success, warn, error 가능
        });
        return;
      }
      this.isShowModal = !this.isShowModal
    },
    //저장하면 입고검사테이블에 추가처리
    async confirm() {
      let rawData = toRaw(this.rowData2);   //[{mName:"당근", ordQty:"100000", ...}, {...}, ...]


      let insertResult = await axios.post(`${ajaxUrl}/insertQCM`, rawData)
        .catch(err => console.log(err));
      notify({
        text: `${insertResult.data.successNum}건이 신청되었습니다`,
        type: "success", // success, warn, error 가능
      });
      this.searchOrderAll() // 저장된 항목 초기화
      this.rowData2 = []; // 저장된 항목 초기화

      this.closeModal();

    },
    //수량 수정 관련
    //신청 건의 합격량, 불합격량(불량항목, 각각의 수량) 처리
    onCellClicked(event) {
      //삭제버튼을 누룰 때 출력X
      if (event.event.target && event.event.target.classList.contains("delete-btn")) {
        return;
      }
      // 선택된 행 데이터 저장 및 모달 표시
      this.selectedRow = JSON.parse(JSON.stringify(event.data));
      if (this.selectedRow.mName.includes('병')) {
        this.materialType = '개';
        this.editedQuantity = this.selectedRow.ordQty;
      } else {
        this.materialType = 'kg';
        this.editedQuantity = this.selectedRow.ordQty * 0.001;

      }
      
      this.isShowModalUpdate = true;
    },

    updateQnt(orderCD, qty) {
      if (qty <= 0) {
        notify({
          text: "검사할 자재 수량이 존재하지 않습니다.",
          type: "warn", // success, warn, error 가능
        });
        return;
      }
      else if (this.materialType === '개' && !Number.isInteger(qty)) {
        notify({
          text: "해당 자재의 수량은 소수로 입력할 수 없습니다.",
          type: "warn", // success, warn, error 가능
        });
        return;
      }
      
      //단위에 따라 다르게 처리
      switch(this.materialType){
        case 'kg':
          qty= qty *1000;
          break;
        case '개':
          break;
      }
      
      const targetIndex = this.rowData2.findIndex(row => row.orderCode === orderCD);
      if (targetIndex !== -1) {
        this.rowData2[targetIndex] = {
          ...this.rowData2[targetIndex],
          ordQty: qty, // 수량만 업데이트
        };
        notify({
          text: "자재의 수량을 수정했습니다.",
          type: "success", // success, warn, error 가능
        });
      }
      this.rowData2 = [...this.rowData2,];
      this.closeModal();
    },
    //모달 닫기
    closeModal() {
      this.isShowModal = false;
      this.isShowModalUpdate = false;
    },



  },
  mounted() {
    this.bringIoginInfo();
  },
  //기본테이블출력
  created() {
    this.searchOrderAll();
  },
};
</script>


<style scoped lang="scss">
.container-fluid {
  min-height: 500px;

}

//검색창 라벨
.mb-4 {
  label {
    font-weight: bold;
    margin-bottom: 5px;
    color: #333;
  }
}

//검색창 배경색
.search-background {
  background-color: #e9ecef;




  input {
    background-color: #ffffff;
    border-radius: 5px;
    padding: 8px 12px;
    font-size: 16px;
    transition: border-color 0.3s;
  }

  input:focus {
    border-color: #007bff;
    outline: none;
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
  }
}

//모달 창 내용
.form-group {
  display: flex;
  flex-direction: column;
  /* 세로 정렬 */
  margin-bottom: 15px;

  /* 그룹 간 여백 */
  label {
    font-weight: bold;
    margin-bottom: 5px;
    color: #333;
  }

  input {
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 8px 12px;
    font-size: 16px;
    transition: border-color 0.3s;
  }

  input:focus {
    border-color: #007bff;
    outline: none;
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
  }
}
</style>